@import url('https://fonts.googleapis.com/css?family=Product+Sans');

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  font-family: 'Product Sans', Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
  font-size: 1rem;
  color: white;
}

* {
  box-sizing: border-box;
}

.container {
  display: flex;
  align-items: center;
  height: 100vh;
  position: relative;
}

.connect {
  padding: 1rem;
  background: rgba(0,0,0,.3);
  border-radius: .5rem;
  text-align: center;
  cursor: pointer;
}

.items {
  width: 35vw;
  border-radius: .5rem;
  background-color: #1db954;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 2rem;
  bottom: 2rem;
  left: 2rem;
}

.items > .title {
  flex: 0 0 auto;
  padding: .5rem 0;
  font-size: 2rem;
  font-weight: 300;
}

.items > .title > svg {
  color: white;
  padding-right: .5rem;
  cursor: pointer;
}

.items > div {
  flex: 1 1 auto;
  overflow-y: scroll;
}

.items .item {
  width: 100%;
  border-radius: 5px;
  background: rgba(0, 0, 0, .2);
  margin: 1rem 0;
  display: flex;
  cursor: pointer;
}

.items .item.active {
  background: rgba(0, 0, 0, .4);
}

.item .image {
  flex: 0 0 auto;
  padding-right: .5rem;
  height: 60px;
}

.image img {
  border-radius: 5px;
  height: 60px;
}

.item .title {
  flex: 1 1 auto;
  padding-top: .5rem;
}

.details {
  position: absolute;
  top: 2rem;
  bottom: 2rem;
  right: 2rem;
  width: 55vw;
  display: flex;
  flex-direction: column;
}

.details > div {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-radius: .5rem;
  margin-bottom: 1rem;
  border: 4px solid rgba(0, 0, 0, .1);
}

.details > div > input {
  position: absolute;
  bottom: 0;
  left: 0;
  cursor: pointer;
  padding: 1rem;
  color: transparent;
}

input:focus {
  outline: none;
}

svg {
  font-size: 5rem;
  color: black;
}

.details > div > img {
  width: 100%;
  border-radius: .5rem;
}

textarea {
  flex: 0 0 auto;
  resize: none;
  border: 0;
  background: rgba(0, 0, 0, .1);
  border-radius: .5rem;
  width: 100%;
  height: 40%;
  padding: 1rem;
  font-size: 1.5rem;
  font-family: 'Product Sans', Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}

textarea:focus {
  outline: none;
}